let getCommonRow = (args = {}) => {
  return Object.assign({}, commonRow, args)
}

function cpArr(arr: object[]) {
  let temp: object[] = [];
  arr.forEach(a => {
    temp.push(toString.call(a) === '[object Object]' ? Object.assign({}, a) : a);
  });
  return temp;
}

function cp(from = {}, to = {}) {
  return Object.assign({}, to, from);
}

